.footer {
	width: 100%;
	color: white;
	background-color: black;
	bottom: 0;

	ul {
		list-style-type: none;
	}

	li a {
		letter-spacing: 1px;
		font-weight: 400;
		font-size: 16px;
		&:hover {
			color: #6e6e6e;
		}
	}

	.info-container-1 {
		padding-top: 54px;
		padding: 54px 0;
	}

	.list-item {
		padding-bottom: 13px;
	}

	.menu,
	.brand-name {
		text-align: center;
	}

	.brand-name {
		font-family: "Noto Sans", Helvetica, Arial, sans-serif;
		font-size: 36px;
		letter-spacing: 0.025em;
		padding-bottom: 20px;
	}
}
